.menubar {
    
    width: 90vw;
    padding:5px;
    display: flex;
    justify-content: space-between;
    border-bottom: 0.5px solid grey;
    margin-bottom: 30px;
};

button{
    padding: 10px;
    width: 20px;
    font-size: 68px;
    height: 1;
    margin-right: 5px;
}

.menuItem:hover{
    padding: 10px;
    background-color: rgb(203, 228, 236);
    color:rgb(9, 43, 54);
    font-size: 32px;
    margin-right: 50px;
};

@media screen and (min-width:768px){
    .menubar{
      grid-template-columns: repeat(3, 1fr);
    }
  }